@charset "utf-8";
/* CSS Document */
body {
	background: #0E1428 url(../images/logo-leaf.png) no-repeat left center;
	background-size: contain;
	font-family: 'Montserrat';
	font-weight: 400;
}

div.loader {
    display: block;
    position: fixed;
    color: #ffffff;
    background-repeat: no-repeat;
    background-position: center;
    bottom: 0;
    right: 0;
    left: 50%;
    top: 50%;
    z-index: 101;
    width: 32px;
    height: 32px;
    margin-left: -16px;
    margin-top: -16px;
    font-size: 25px;
}

