/* centered columns styles */
.parent {
	display: table;
}
.child {
	display: table-cell;
	vertical-align: middle;
}
/* Heading styles starts */
h1.h1 {
	font-weight: 300;
	font-size: 22px;
}
/* Heading styles End */

/* Font Size styles starts */
.font-size11 {
	font-size: 11px;
}
.font-size12 {
	font-size: 12px;
}
.font-size14 {
	font-size: 14px;
}
/* Font Size styles ends */
/* Login Page styles */
.login-font {
	color: #C3C3C3;
	font-weight: normal;
}
.login-font a {
	color: #C3C3C3;
}
.login-font .btn {
	width: 60%;
	font-size: 14px;
	text-align: left;
}
.login-font .btn i {
	float: right;
	margin-top: 2px;
}
hr {
	border-top: 2px solid #535660;
}
/* Login Page styles Ends */
.btn-custom {
	background: linear-gradient(180deg, #23cbd8 0%, #18b4c7 100%);
	border: 1px solid #1ab8ca;
}
.btn-custom:hover {
	background: linear-gradient(180deg, #22efff 0%, #18b4c7 100%);
	border: 1px solid #1ab8ca;
}

button[disabled] {
    opacity: .65;
    cursor: not-allowed;
}

control-messages div {
    color: #E82C0C;
    margin: 6px 0;
}

form.ng-invalid {
    border: 0;
}

.ng-invalid {
    border: 2px solid #a94442;
}

.alert {
    font-size: 13px;
    padding: 10px 15px;
    border: none;
}

.alert-danger {
    color: #a94442;
    background-color: #f2dede;
    border-color: #ebccd1;
}

.alert {
    padding: 15px;
    margin-bottom: 20px;
    border: 1px solid transparent;
    border-radius: 4px;
}